import axios from 'axios';

const url = 'https://www.instagram.com/adnanfajlur/?__a=1';

export const getData = () => {
  let hasil = false;
  let failure = false;
  axios.get(url)
    .then(response => {
      hasil = response;
    })
    .catch(error => {
      console.log(error);
      failure = error;
    });
  return { hasil, failure };
};



